@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap');

*{
  font-family: 'Noto Sans KR', sans-serif;
}

option{
  width: 100%;
  color: #000;
  background-color: #fff;
}

.popup{
  width: 200px;
  height: 35px;
  background-color: #fff;
  border-radius: 10px;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 20px;
  font-weight: bold;
  padding: 5px;
  margin-bottom: 25px;
}

.popup .tit, .count{
  transition: all 0.3s ease-in-out;
}

.popup .line{
  width: 2px;
  height: 25px;
  background-color: #ddd;
  margin-right: 30px;
}

#crime{
  -moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
  
  color: #ff0000;
  text-align: center;
  width: 25%;
  height: 70px;
  background-color: #282c34;
  font-size: 47.4px;
  font-weight: bold;
  margin: 0 10px;
  outline: none;
  border: none;
}

.dg-map svg polyline, path{
  transition: all 0.3s ease-in-out;
}

.App {
  text-align: center;
  width: 100%; height: 100vh;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
